import React, {Component} from 'react';
import {resetFooterStyle, resetNavStyle} from "../utils/utils";
import {Col, Container, Row} from "react-bootstrap";
import {Toolbar, Box, AppBar, Tabs, Tab} from "@material-ui/core";

const TabPanel = (props) => {
  const {children, value, index, ...other} = props;
  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          {children}
        </Box>
      )}
    </div>
  );
}

const a11yProps = (index) => {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

class GCPRank extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: 0,
    }
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(value) {
    this.setState({value});
  }

  componentDidMount() {
    document.title = 'GCP Rank - DSC MESCOE';
    resetNavStyle({page: 'GCP Rank'});
  }

  medals = () => <Row>
    <Col>
      <Row>
        <span className='medal platinum'/>
        <p className='my-auto'>Platinum</p>
      </Row>
    </Col>
    <Col>
      <Row>
        <span className='medal gold'/>
        <p className='my-auto'>Gold</p>
      </Row>
    </Col>
    <Col>
      <Row>
        <span className='medal silver'/>
        <p className='my-auto'>Silver</p>
      </Row>
    </Col>
    <Col>
      <Row>
        <span className='medal bronze'/>
        <p className='my-auto'>Bronze</p>
      </Row>
    </Col>
  </Row>;

  render() {
    const footer = document.querySelector('#contact');
    if (footer) {
      resetFooterStyle()
    }
    return (
      <Toolbar className='grid'>
        <Container>
          <Row className="mt-5">
            <Col>
              <h3 style={{color: '#8a3fff'}} className='project'>30 Days of Google Cloud</h3>
            </Col>
          </Row>
          <Row className='mb-3'/>
          <AppBar position='static' color='inherit'>
            <Tabs
              TabIndicatorProps={{
                style: {
                  backgroundColor: '#8a3fff',
                  height: 4
                }
              }}
              value={this.state.value}
              aria-label="fashion tabs"
              variant="scrollable"
            >
              <Tab label="Cloud Engineering" {...a11yProps(0)} style={{fontFamily: 'Google Sans', fontWeight: 'bold'}}
                   onClick={() => {
                     this.handleChange(0)
                   }}/>
              <Tab label="Data Science and ML" {...a11yProps(1)} style={{fontFamily: 'Google Sans', fontWeight: 'bold'}}
                   onClick={() => {
                     this.handleChange(1)
                   }}/>
            </Tabs>
          </AppBar>
          <TabPanel value={this.state.value} index={0} style={{backgroundColor: 'white'}}
                    className='mb-5 MuiPaper-elevation3'>
            <Container>
              <Row>
                <Col xl={3}>
                  <h4 style={{color: '#222'}} className='project'>Cloud Engineering</h4>
                </Col>
                <Col>
                  {this.medals()}
                </Col>
              </Row>
            </Container>
          </TabPanel>
          <TabPanel value={this.state.value} index={1} style={{backgroundColor: 'white'}}
                    className='mb-5 MuiPaper-elevation3'>
            <Container>
              <Row>
                <Col xl={5}>
                  <h4 style={{color: '#222'}} className='project'>Data Science and Machine Learning</h4>
                </Col>
                <Col>
                  {this.medals()}
                </Col>
              </Row>
            </Container>
          </TabPanel>
          <Row className='mb-5'/>
        </Container>
      </Toolbar>
    );
  }
}

export default GCPRank;